










import { Component, Watch, Vue } from "vue-property-decorator";
import AppBar from "./components/app-bar.vue";
import NavigationDrawer from "./components/navigation-drawer.vue";
import { Route } from "vue-router";
import { CollectionNamesArr } from "./services/meiosis";

@Component({
  components: {
    AppBar,
    NavigationDrawer,
  },
})
export default class App extends Vue {
  private theme: string = "";

  constructor() {
    super();
    CollectionNamesArr.forEach(async (n) => {
      await this.$store.actions[n].updateList();
    });
  }

  @Watch("theme")
  themeUpdated(theme: string) {
    this.$vuetify.theme.dark = theme === "dark" ? true : false;
  }

  @Watch("$route")
  routeUpdated(to: Route, from: Route) {
    if (`/${this.$store.states().app.route}` !== to.path) {
      this.$store.actions.changePage(to.path);
    }
  }

  public mounted() {
    this.$store.states.map((s) => {
      this.theme = s.app.theme;
    });
  }
}
