










import { lightFormat } from "date-fns";
import { Component, Prop, Vue, Watch } from "vue-property-decorator";
import { getUuid } from "../utils/constants";

@Component({
  components: {},
})
export default class ChartCard extends Vue {
  private title: string = "Chart";
  private time: number = 0;
  private timer?: any;
  private activeWord: number = -1;
  private words: string[] = [];

  constructor() {
    super();
  }

  private async init() {
    if (!!this.timer) return;
    this.timer = setInterval(() => {
      this.time = Date.now();
    }, 500);
  }

  private formattedTime(): string {
    return lightFormat(this.time, "yyyy-MM-dd '@' HH:mm:ss");
  }

  private addActor(): void {
    this.$store.actions.actors.save({ id: getUuid(), name: "Hannibal", type: 'actors' });
  }

  destroyed() {
    if (!!this.timer) return;
    clearInterval(this.timer);
  }

  mounted() {
    this.init();
  }
}
